body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {

}

*{
    box-sizing: border-box;
}
.Home{

}
.Home .bodyContainer{
    padding: 20px;
}
.Home .pinsContainer{
    -webkit-column-count: 4;
            column-count: 4;
    padding: 20px 60px;
}
.Home .Pin{
    display: inline-block;
}

@media  only screen and  (max-width: 1100px){
    .Home .pinsContainer{
        -webkit-column-count: 3;
                column-count: 3;
    }
}

@media  only screen and  (max-width: 850px){
    .Home .pinsContainer{
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media  only screen and  (max-width: 650px){
    .Home .pinsContainer{
        -webkit-column-count: 1;
                column-count: 1;
    }
}

.Navbar{
    width: 100%;
    background-color: #333;
    display: -webkit-flex;
    display: flex;
    /* justify-content: flex-end; */
    /* align-items: center; */
    padding: 10px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.Navbar .leftContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: 1 1 50%;
            flex: 1 1 50%;
}

.Navbar .leftContainer a{
    margin: 12px 16px;
    color: white;
    text-decoration: none;
}

.Navbar .rightContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: 1 1 50%;
            flex: 1 1 50%;
}
.Navbar .leftContainer a:hover{
    color: #d6d6d6;
}

.Navbar .rightContainer button{
    margin: 0 16px;
}

@media(max-width: 767px){
    .Navbar{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .Navbar .leftContainer{
        -webkit-justify-content: center;
                justify-content: center;
    }
    .Navbar .rightContainer{
        -webkit-justify-content: center;
                justify-content: center;
    }
  
  }

.Modal{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 100;
}

.Modal .container{
    background-color: white;
    width: 800px;
    border-radius: 10px;
    border: 1px solid black;
    position: relative;
}
.Modal .header{
    height: 80px;
    font-size: 30px;
    border-bottom: 1px solid #b7b7b7;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.Modal .footer{
    padding: 10px;
    border-top: 1px solid #b7b7b7;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}
.Modal .close{
    position: absolute;
    right: 28px;
    top: 10px;
    color: #595959;
    font-size: 30px;
    cursor: pointer;
}
.Button{

}

.Button .btn{
    border: none;
    padding: 10px 24px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius: 4px;
}
.Button .btn-primary{
    color: white;
    background-color: #F59D18;
}
.Button .btn-primary:hover{
    background-color: #E8891C;
}
.Button .btn-primary:active, .Button .btn-primary:focus, .Button .btn-primary:active:focus, .Button .btn-primary:active:hover{
    background-color: #d17729;
}

.Button .btn-danger{
    color: white;
    background-color: #ef4545;
}
.Button .btn-danger:hover{
    background-color: #d83636;
}
.Button .btn-danger:active, .Button .btn-danger:focus, .Button .btn-danger:active:focus, .Button .btn-danger:active:hover{
    background-color: #bf2a2a;
}


.Input{
    width: 100%;
    padding: 5px;
}
.Input label{
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
}
.Input .inputElement{
    outline: none;
    border: 1px solid #cccccc;
    border-radius: 4px;
    background-color: white;
    padding: 8px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}
.Input .inputElement:focus{
    border-color: #5f5f5f;
}

.Pin{
    width: 100%;
    position: relative;
    margin: 4px;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
}
.Pin:hover{
    background-color: #e2e2e2;
}
.Pin img{
    width: 100%;
    border-radius: 10px;
}
.Pin img:hover{
    background-color: red;
}
.Pin button{
    display: none;
    position: absolute;
    top: 20px;
    right: 20px;
}
.Pin .caption{
    display: none;
    position: absolute;
    bottom: 28px;
    left: 28px;
    color: white;
    background-color: #3f3f3f;
    padding: 6px;
    border-radius: 100px;
    cursor: pointer;
}
.Pin:hover .caption{
    display: block;
}
.Pin:hover button{
    display: block;
}

